import React, { Component } from "react";
import { Row, Col } from "antd";
import { withTranslation } from "react-i18next";
import { Carousel, Timeline } from "antd";
import { renderContentLanguage } from "../../../../common/js/function";
import {
  RightCircleFilled,
  LeftCircleFilled,
  FacebookFilled,
  InstagramFilled,
  MailFilled
} from "@ant-design/icons";

const ENG = "ENGLISH ENGLISH ENGLISH ENGLISH ENGLISH ENGLISH ENGLISH ENGLISH ENGLISH";
const VN = "VN VN VN VN VN VN VN VN VN VN VN VN VN VN VN VN VN VVN VN";

class TopContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {
        name: "Du Sainbolt",
        url_avatar:
          "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRH0jj8tNxcDI8aKOESEgrPGna-RMgUvNCs8w&usqp=CAU",
      },
    };
    this.carousel = React.createRef();
  }

  next = () => {
    this.carousel.next();
  };
  previous = () => {
    this.carousel.prev();
  };

  renderContentCourse = () => {
    const { t } = this.props;
    return (
      <Row>
        <Col className="team-carouse--wrapper" lg={8} xl={10} xs={24}>
          <div className="team-carouse__content">
            <label className="team-carouse__content--title">
              Chief Executive Officer
            </label>
            <p className="team-carouse__content--description">
              {renderContentLanguage(VN, ENG, t("language.value"))}
            </p>
          </div>
        </Col>
        <Col className="team-carouse--wrapper" lg={16} xl={14} xs={24}>
          {this.genderContentRight()}
        </Col>
      </Row>
    );
  };

  genderContentRight = () => {
    const { t } = this.props;
    return (
      <div className="team-carouse--card">
        <Row>
          <Col className="team-carouse--image-item" xl={8} xll={6} lg={12} sm={12} xs={24}>
            <img
              alt="img_team"
              className="team-carouse--card__image"
              src="data:image/jpeg;base64,"
            />
            <div className="team-carouse--wrapper-social">
              <div className="team-carouse--wrapper-social__group">
                <a className="icon_team" href={"http://facebook.com"} rel="noreferrer" target="_blank">
                  <FacebookFilled />
                </a>
                <a className="icon_team" href={"http://facebook.com"} rel="noreferrer" target="_blank">
                  <InstagramFilled  />
                </a>
                <a className="icon_team" href={"http://facebook.com"} rel="noreferrer" target="_blank">
                  <MailFilled  />
                </a>
              </div>
            </div>
          </Col>
          <Col xl={16} lg={12} sm={12} xs={24} xll={18}>
            <Timeline className="team-carouse--time-line">
              <Timeline.Item>
                <label className="team-carouse--time-line__title">
                  {t("landing_page.label_birthday")}:
                </label>
                18/11/1999
              </Timeline.Item>
              <Timeline.Item>
                <label className="team-carouse--time-line__title">
                  {t("landing_page.label_gender")}:
                </label>
                Nam
              </Timeline.Item>
              <Timeline.Item>
                <label className="team-carouse--time-line__title">
                  {t("landing_page.label_university")}:
                </label>
                Học viện kỹ thuật mật mã
              </Timeline.Item>
              <Timeline.Item>
                <label className="team-carouse--time-line__title">
                  {t("landing_page.label_skill")}:
                </label>
                Lập trình ứng dụng web
              </Timeline.Item>
              <Timeline.Item>
                <label className="team-carouse--time-line__title">
                  {t("landing_page.label_address")}:
                </label>
                Hà Nội, Việt Nam
              </Timeline.Item>
            </Timeline>
          </Col>
        </Row>
      </div>
    );
  };

  render() {
    return (
      <div className="container-ld--rows__course team-carouse">
        <LeftCircleFilled
          className="team-carouse--icon-left"
          onClick={this.previous}
        />
        <Carousel
          draggable={true}
          ref={node => (this.carousel = node)}
          speed={500}
          effect="scrolling"
        >
          <Col span={24}>{this.renderContentCourse()}</Col>
          <Col span={24}>{this.renderContentCourse()}</Col>
          <Col span={24}>{this.renderContentCourse()}</Col>
        </Carousel>
        <RightCircleFilled
          onClick={this.next}
          className="team-carouse--icon-right"
        />
      </div>
    );
  }
}

export default withTranslation()(TopContent);
